





















import { Action } from 'vuex-class';
import { Component, Ref, Vue } from 'vue-property-decorator';
import Swal from 'sweetalert2';
import banner from '@/components/Banner.vue';
import * as commonTypes from '@/types/common';
import { IErrorReport } from '@/types/interface';
import inputFormPanel from '../../components/InputFormPanel.vue';

@Component({
  components: {
    banner,
    inputFormPanel,
  },
})

export default class ErrorReport extends Vue {
  @Ref('inputForm') readonly inputForm!: inputFormPanel;
  @Action('postMemberMisReport', { namespace: 'pastTest' }) postMemberMisReport!: (params: IErrorReport) => Promise<void>;

  inputList = [commonTypes.INPUT_CK_QUZ_ID, commonTypes.INPUT_EMAIL,
    commonTypes.INPUT_CK_CONSULT];

  close() {
    this.$router.push('/test/solution_page');
  }

  confirm() {
    const info: IErrorReport | undefined = this.inputForm.getErrorReportInput();
    if (info) {
      this.postMemberMisReport(info)
        .then(() => {
          Swal.fire({
            width: 300,
            showCloseButton: false,
            background: '#FFFFFF',
            title: '回報完成',
            customClass: {
              title: 'modal-title',
              content: 'modal-content',
              confirmButton: 'modal-confirm-button',
            },
            confirmButtonText: '確定',
          }).then((res) => {
            if (res.isConfirmed) this.$router.push('/test/solution_page');
          });
        })
        .catch(() => {
          Swal.fire({
            width: 300,
            showCloseButton: false,
            background: '#FFFFFF',
            title: '回報失敗',
            customClass: {
              title: 'modal-title',
              content: 'modal-content',
              confirmButton: 'modal-confirm-button',
            },
            confirmButtonText: '確定',
          }).then((res) => {
            if (res.isConfirmed) this.$router.push('/test/solution_page');
          });
        });
    }
  }
}
